import { $scheduleInput, $inputEnterButton, $scheduleList } from "./index";

export let scheduleList: string[] = [];

export const SubmitSchedule = () => {
  const content = $scheduleInput.value;
  if (content.length == 0) {
    return;
  }
  scheduleList.push(content);
  $scheduleInput.value = "";
  updateScheduleList();
};

export const updateScheduleList = () => {
  $scheduleList.innerHTML = "";
  scheduleList.map((schedule) => {
    const $item = document.createElement("li");
    const textNode = document.createTextNode(schedule);
    $item.appendChild(textNode);
    $scheduleList.appendChild($item);
  });
};
